import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			withDataBuys : [],
			withDataSells : []
		};
	},
	methods: {
		cancelStock(el,type){
			if(el.flag != 'N'){
				return;
			}
			let _this = this;
			uni.showModal({
				title: '请确认',
				content: '是否撤单？',
				success: function (res) {
					if (res.confirm) {
						http.get('transaction/cancel',{id:el.id,type:type}).then((res)=>{
							uni.showModal({
								title : '提示',
								content : '撤单成功',
								showCancel : false,
							})
							_this.init();
						})
					} else if (res.cancel) {
						
					}
				}
			});
		},
		init(){
			uni.showLoading({
				mask:true
			})
			http.get('transaction/tocancel',{phone:this.$store.state.userInfo.phone}).then((res)=>{
				this.withDataBuys = res.data.data.buys;
				this.withDataSells = res.data.data.sells;
			})
		}
	},
	onLoad() {
		this.init();
	},
	filters: {
　　　　 timeZhuan(value) {
			   return value.substring(0,10);
　　　　 }
　　}
}
